.home {
    width: 100%;
    height: 90vh;
    display: flex;
    justify-content: center;
    align-items: left;
    flex-direction: column;
    background-position: center;
    background-size: cover;
    font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  }
  
  .headerContainer {
    width: auto; 
    margin-left: 80px;
  }
  
  .home .headerContainer h1 {
    justify-content: center;
    align-items: center;
    font-size: 100px;
    height: 60px;
    font-weight: 50;
    color:black;
    margin-left: 40px;
  }
  
  .home .headerContainer p {
    padding: 0;
    font-size: 55px;
    font-weight: lighter;
    color: black;
    margin-left: 40px;
    margin-top: 90px;
  }
  
  .headerContainer button {
    color: black;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    justify-content: center;
    background-color: #121619;
    height: 50px;
    width: 180px;
    color: white;
    text-decoration: none;
    font-size: 20px;
    margin-left: 40px;
  }
  
  .headerContainer button:hover {
    background-color: red;
    cursor: pointer;
    transition: 0.3s ease-in;
  }
  
  @media only screen and (max-width: 650px) {
    .home {
      justify-content: center;
      align-items: center;
    }
    
  }