.home {
    width: 100%;
    height: 90vh;
    display: flex;
    justify-content: center;
    align-items: left;
    flex-direction: column;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  }
  
  .hContainer {
    width: auto; 
    margin-left: 80px;
  }

  .hContainer h1 {
    justify-content: center;
    align-items: center;
    font-size: 65px;
    height: 60px;
    font-weight: 50;
    color: white;
    margin-left: 40px;
    margin-top: 50px;
  }
  
  .hContainer p {
    padding: 0;
    font-size: 25px;
    text-align: left;
    font-weight: lighter;
    color: white;
    margin-left: 40px;
    margin-right: 510px;
  }
  
  .hContainer button {
    color: black;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    justify-content: center;
    background-color: red;
    height: 50px;
    width: 180px;
    text-decoration: none;
    font-size: 20px;
    margin-top: 40px;
    margin-left: 40px;
  }

  .hContainer button:hover{
    background-color: white;
    cursor: pointer;
    transition: 0.3s ease-in;
  }
  
  @media only screen and (max-width: 650px) {
    .home {
      justify-content: center;
      align-items: center;
    }
   
  }