  .about {
    width: 100%;
    height: 90vh;
    display: flex;
    flex-direction: column;
  }
  
  .aboutTop {
    width: 100%;
    height: 45%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  }
  
  .aboutText {
    display: flex;
    flex-direction: column;
    height: 55%;
    align-items: center;
    margin-top: 20px;
  }
  
  .aboutText p {
    width: 1000px;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 40;
    font-size: 20px;
    align-items: center;
    color: black;
    height: 30px;
    display: flex;
    margin-top: 40px;
    justify-content: center;
    transform: translateY(-40px);
  }
  
  .about .aboutText h1 {
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 50;
    font-size: 40px;
    align-items: center;
    color: black;
    height: 30px;
    display: flex;
    margin-top: 60px;
    justify-content: center;
    transform: translateY(-40px);
  }

  